
import vte from '../components/vte.vue'

const components = [
    vte
]
 
const install = function (Vue) {
    // 
    components.forEach(item => {
        Vue.component(item.name, item)
    })
}
// CDN Vue.use() CommonJS  Vue.use()
if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue)
}
export default install